/* Common CSS for all files */
html {
	font-size: 62.5%; /* 16px x 62.5 = 10px = 1rem */
	box-sizing: border-box;
}
body {
	margin: 0;
	height: 100vh;
	font-size: 1.6rem;
	font-family: Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
#root {
	height: 100%;
}

/* Common */
h1 {
	font-size: 1.8rem;
	padding: 1rem 0;
}
h2 {
	font-size: 1.6rem;
	padding: 1rem 0;
}

a {
	text-decoration: none;
}

ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
li {
	margin-top: 1rem;
}

input,
select,
textarea,
button {
	padding: 1rem;
	border-radius: 0.5rem;
	border: 0.1rem #a4a4a4 solid;
	font-size: 1.6rem;
	font-family: Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
button {
	padding: 10px;
	background-color: #203040;
	color: white;
	cursor: pointer;
}

button.primary {
	background-color: #203040;
}
button.block {
	width: 100%;
}
button.small {
	font-size: 1.2rem;
}

/* Sidebar */

.brand button {
	font-size: 3rem;
	padding: 0.5rem;
	background: none;
	border: none;
	color: #ffffff;
	cursor: pointer;
}
.sidebar {
	position: fixed;
	transition: all 0.5s;
	transform: translateX(-30rem);
	width: 30rem;
	background-color: #f0f0f0;
	height: 100%;
}
.sidebar.open {
	transform: translateX(0);
}

.sidebar-close-button {
	border-radius: 50%;
	border: 0.1rem #000000 solid;
	width: 3rem;
	height: 3rem;
	padding: 0.5rem;
	font-size: 2rem;
	padding-top: 0;
	cursor: pointer;
	position: absolute;
	right: 0.5rem;
	top: 1.5rem;
}
/* Aside */
aside {
	position: fixed;
	width: 30rem;
	height: 100%;
	background-color: #efefef;
	z-index: 1000;
	transform: translateX(-30rem);
	transition: all 0.5s;
}
aside.open {
	transform: translateX(0);
}
button.open-sidebar {
	font-size: 3rem;
	padding: 0.2rem 0.5rem;
	margin: 0 0.5rem;
	background: none;
	color: #ffffff;
	cursor: pointer;
}
button.open-sidebar:hover {
	border-color: #ffffff;
}
aside ul {
	padding: 0;
	list-style: none;
}
aside li {
	display: flex;
	justify-content: space-between;
	padding: 1rem;
}
button.close-sidebar {
	padding: 0.3rem 0.8rem;
}

/* Image */
img {
	border-radius: 0.5rem;
}
img.small {
	max-width: 5rem;
	width: 100%;
}
img.medium {
	max-width: 29rem;
	width: 100%;
}
img.large {
	width: 100%;
}

