/* Rating */
.rating span {
	color: #f0c040;
	margin: 0.1rem;
}
.rating span:last-child {
	color: #404040;
}
/* Alert */
.loading {
	display: block !important;
}
.success {
	color: #20a020;
}
.danger {
	color: #a02020;
}
.alert {
	padding: 1rem;
	border: 0.1rem solid transparent;
	border-radius: 0.5rem;
}
.alert-info {
	color: #2020a0;
	background-color: #e0e0ff;
}
.alert-danger {
	color: #a02020;
	background-color: #ffe0e0;
}
.alert-success {
	color: #20a020;
	background-color: #eeffe0;
}
/* Form */
.form {
	max-width: 60rem;
	margin: 0 auto;
}
.form > div {
	display: flex;
	flex-direction: column;
	margin: 1rem;
}
.form label {
	margin: 1rem 0;
}
/* Dropdown */
.dropdown {
	display: inline-block;
	position: relative;
}
.dropdown-content {
	position: absolute;
	display: none;
	right: 0;
	min-width: 12rem;
	padding: 1rem;
	z-index: 1;
	background-color: #203040;
	margin: 0;
	margin-top: 0.4rem;
	border-radius: 0.5rem;
}
.dropdown:hover .dropdown-content {
	display: block;
}

/* Place Order */

.placeorder {
	display: flex;
	flex-wrap: wrap;
	padding: 1rem;
	justify-content: space-between;
}
.placeorder-info {
	flex: 3 1 60rem;
}
.placeorder-action {
	flex: 1 1 20rem;
	border: 0.1rem #c0c0c0 solid;
	border-radius: 0.5rem;
	background-color: #fcfcfc;
	padding: 1rem;
}
.placeorder-info > div {
	border: 0.1rem #c0c0c0 solid;
	border-radius: 0.5rem;
	background-color: #fcfcfc;
	padding: 1rem;
	margin: 1rem;
}
.placeorder-info > div:first-child {
	margin-top: 0;
}
.placeorder-action > ul {
	padding: 0;
	list-style-type: none;
}
.placeorder-action > ul > li {
	display: flex;
	justify-content: space-between;
	margin-bottom: 1rem;
}
.placeorder-action > ul > li:last-child {
	font-size: 2rem;
	font-weight: bold;
	color: #c04000;
}
.placeorder-actions-payment > div {
	width: 100%;
}

/* Table */
.table {
	width: 100%;
	border-collapse: collapse;
}
.table tbody tr:nth-of-type(odd) {
	background-color: #f4f4f4;
}
.table td,
.table th {
	text-align: left;
	border: 0.1rem solid #e4e4e4;
	padding: 0.5rem;
}
.table button {
	margin: 0 0.2rem;
}
/* Carousel */
.carousel .slide img {
	max-width: 30rem;
}

/* Search */
.search button {
	border-radius: 0 0.5rem 0.5rem 0;
	border-right: none;
	margin-right: 0.5rem;
}
.search input {
	border-radius: 0.5rem 0 0 0.5rem;

	border-right: none;
	margin-left: 0.5rem;
}
.active {
	font-weight: bold;
}

/* Map */
.full-container {
	margin: -1rem;
	height: calc(100% + 2rem);
}
.map-input-box {
	box-sizing: border-box;
	position: absolute;
	left: 0;
	right: 0;
	margin: 0.5rem auto;
	width: 25rem;
	height: 4rem;
	display: flex;
}
.map-input-box input {
	border-radius: 1rem 0 0 1rem;
	border-right: 0;
}
.map-input-box button {
	border-radius: 0 1rem 1rem 0;
	border-left: 0;
}
/* Pagination */
.pagination a {
	padding: 1rem;
	margin: 0.5rem;
	border-radius: 0.5rem;
	border: 0.1rem #a4a4a4 solid;
	font-size: 1.6rem;
	font-family: Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.pagination a.active {
	font-weight: bold;
}

/* Dashboard */

.summary > li {
	border: 0.1rem #c0c0c0 solid;
	margin: 2rem;
	border-radius: 0.5rem;
	flex: 1 1 20rem;
}
.summary-title {
	font-size: 2rem;
	padding: 1rem;
}
.summary-body {
	font-size: 4rem;
	padding: 1rem;
	text-align: center;
}

.summary-title.color1 {
	background-color: #f0e0e0;
}
.summary-title.color2 {
	background-color: #e0f0e0;
}
.summary-title.color3 {
	background-color: #e0e0f0;
}

/* Chatbox */
.chatbox {
	color: #000000;
	position: fixed;
	right: 1rem;
	bottom: 1rem;
}
.chatbox ul {
	overflow: scroll;
	max-height: 20rem;
}
.chatbox li {
	margin-bottom: 1rem;
}
.chatbox input {
	width: calc(100% - 9rem);
}

.support-users {
	background: #f0f0f0;
	height: 100%;
}
.support-users li {
	background-color: #f8f8f8;
}
.support-users button {
	background-color: transparent;
	border: none;
	text-align: left;
}
.support-users li {
	margin: 0;
	background-color: #f0f0f0;
	border-bottom: 0.1rem #c0c0c0 solid;
}

.support-users li:hover {
	background-color: #f0f0f0;
}
.support-users li.selected {
	background-color: #c0c0c0;
}
.support-messages {
	padding: 1rem;
}
.support-messages input {
	width: calc(100% - 9rem);
}
.support-messages ul {
	height: calc(100vh - 18rem);
	max-height: calc(100vh - 18rem);
	overflow: scroll;
}
.support-messages li {
	margin-bottom: 1rem;
}

.support-users span {
	width: 2rem;
	height: 2rem;
	border-radius: 50%;
	position: absolute;
	margin-left: -25px;
	margin-top: 10px;
}
.support-users .offline {
	background-color: #808080;
}
.support-users .online {
	background-color: #20a020;
}
.support-users .unread {
	background-color: #f02020;
}

.filter {
	display: flex;
	list-style-type: none;
	padding: 0;
	margin: 1rem auto;
	justify-content: flex-end;
	align-items: center;
}
.filter input,
.filter button,
.filter select {
	padding: 1rem;
	border-radius: 0.5rem;
	border: 0.1rem #c0c0c0 solid;
	font-size: 1.6rem;
}
.categories {
	padding: 0;
	list-style-type: none;
}
.categories a {
	display: flex;
	padding: 1rem;
}

.categories a:hover {
	background-color: #c0c0c0;
}
